body {
  font-family: var(--fuente-mulish), sans-serif;
  line-height: 1.6;
  background-color: var(--blanco);
  overflow-x: hidden;
  font-size: 16px;
  color: var(--negro-2);
}
body:before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  right: 0;
  bottom: 0;
  background: var(--negro-2);
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}
body.offcanvas-menu:before {
  opacity: 1;
  visibility: visible;
}

.text-black {
  color: var(--negro-1) !important;
}

.text-secondary {
  color: var(--verde-1) !important;
}

.bg-primary-light {
  background-color: rgba(15, 183, 141, 0.05);
}

.bg-secondary-light {
  background-color: rgba(18, 148, 127, 0.05);
}

a {
  color: var(--rojo-2);
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  transition: 0.3s all ease;
}
a:hover {
  color: var(--rojo-2);
  text-decoration: none;
}

::-moz-selection {
  background: var(--negro-1);
  color: var(--blanco);
}

::selection {
  background: var(--negro-1);
  color: var(--blanco);
}

.text-primary {
  color: var(--rojo-2) !important;
}

a.text-primary {
  color: var(--rojo-2) !important;
}
a.text-primary:hover {
  color: var(--rojo-2) !important;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-family: var(--fuente-libre), serif;
  color: var(--negro-1);
}

.boton {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 30px;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.05rem;
}
.boton:active,
.boton:focus {
  outline: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

.boton.boton-personalizado {
  background: var(--rojo-2);
  border-color: var(--rojo-2);
  color: var(--blanco);
}

.boton.boton-personalizado:hover {
  border-color: var(--rojo-1);
  background: var(--rojo-1);
}

.boton.boton-sm {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
}
.boton.boton-outline-personalizado {
  border: 1px solid var(--rojo-2);
  color: var(--rojo-2);
}
.boton.boton-outline-personalizado:hover {
  background: var(--rojo-2);
  color: var(--blanco);
}

.form-control,
.custom-select {
  border: none;
  font-size: 16px;
  height: 45px;
  background: transparent;
  border: 1px solid var(--negro-3);
  border-radius: 0;
  color: var(--negro-1);
}
.form-control:active,
.form-control:focus,
.custom-select:active,
.custom-select:focus {
  color: var(--negro-1);
  border: 1px solid var(--rojo-2);
  -webkit-box-shadow: none;
  box-shadow: none;
  background: transparent;
}
.form-control::-webkit-input-placeholder,
.custom-select::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, 0.5);
}
.form-control::-moz-placeholder,
.custom-select::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(0, 0, 0, 0.5);
}
.form-control:-ms-input-placeholder,
.custom-select:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(0, 0, 0, 0.5);
}
.form-control:-moz-placeholder,
.custom-select:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(0, 0, 0, 0.5);
}

