  bottom: 40px;
}
.owl-single.dots-absolute .owl-dots .owl-dot span,
.owl-carousel.dots-absolute .owl-dots .owl-dot span {
  background: rgba(255, 255, 255, 0.5);
}

.owl-single .owl-dots,
.owl-carousel .owl-dots {
  text-align: center;
  position: absolute;
  width: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
}
.owl-single .owl-dots .owl-dot,
.owl-carousel .owl-dots .owl-dot {
  display: inline-block;
  margin: 5px;
}
.owl-single .owl-dots .owl-dot span,
.owl-carousel .owl-dots .owl-dot span {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--negro-2);
}
.owl-single .owl-dots .owl-dot.active span,
.owl-carousel .owl-dots .owl-dot.active span {
  background: var(--rojo-2);
}

.owl-single.no-dots .owl-dots,
.owl-carousel.no-dots .owl-dots {
  display: none;
}

.owl-single.no-nav .owl-nav,
.owl-carousel.no-nav .owl-nav {
  display: none;
}

.testimonial {
  max-width: 500px;
  text-align: center;
  margin-bottom: 30px;
}
.testimonial .name {
  font-size: 18px;
  color: var(--negro-1);
}
.testimonial .img-wrap img {
  margin: 0 auto;
  width: 70px;
  border-radius: 50%;
}

.list-check li {
  display: block;
  padding-left: 30px;
  position: relative;
}
.list-check li:before {
  content: "\e5ca";
  font-family: "icomoon";
  position: absolute;
  top: -0.3rem;
  font-size: 20px;
  left: 0;
}

.owl-single .owl-nav {
  position: absolute;
  top: 50%;
  width: 100%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
@media (max-width: 767.98px) {
  .owl-single .owl-nav {
    display: none;
  }
}
.owl-single .owl-nav .owl-prev,
.owl-single .owl-nav .owl-next {
  position: absolute;
  top: 0;
}
.owl-single .owl-nav .owl-prev span,
.owl-single .owl-nav .owl-next span {
  display: block;
  background: var(--blanco);
  padding: 15px;
}
.owl-single .owl-nav .owl-prev {
  left: -30px;
}
.owl-single .owl-nav .owl-next {
  right: -30px;
}
.owl-single .owl-nav .owl-next > span {
  position: relative;
  -webkit-transform: rotate(-180deg);
  -ms-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

.ul-check li {
  position: relative;
  padding-left: 30px;
  margin-bottom: 10px;
}
.ul-check li:before {
  position: absolute;
  left: 0;
  top: -0.2rem;
  content: "\e5ca";
  font-size: 20px;
  font-family: "icomoon";
}

.ul-check.primary li:before {
  color: var(--rojo-2);
}

.ul-check.white li:before {
  color: var(--blanco);
}

.section-title {
  color: var(--negro-1);
  margin-bottom: 30px;
}

.site-footer {
  background: var(--rojo-2);
  font-size: 14px;
  color: var(--blanco);
  padding: 70px 0;
}
.site-footer a {
  color: var(--rojo-2);
  position: relative;
  display: inline-block;
}
.site-footer .btn:before {
  display: none;
}
.site-footer .widget {
  margin-bottom: 40px;
  display: block;
  position: relative;
  z-index: 1;
}
.site-footer .widget h3 {
  font-size: 16px;
  color: var(--blanco);
  margin-bottom: 30px;
}
.site-footer .widget .links {
  width: 150px;
}
.site-footer .widget .links li {
  margin-bottom: 10px;
}
.site-footer .widget .links li a {
  color: rgba(255, 255, 255, 0.5);
}
.site-footer .widget .links li a:hover {
  color: var(--blanco);
}
.site-footer .social li {
  display: inline-block;
}
.site-footer .social li a {
  display: inline-block;
  width: 30px;
  height: 30px;
  position: relative;
  background-color: var(--blanco);
  border-radius: 50%;
  color: var(--negro-1);
}
.site-footer .social li a:before {
  display: none;
}
.site-footer .social li a > span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.site-footer .social li a:hover {
  background: var(--verde-5);
  color: var(--blanco);
}

@media (max-width: 991.98px) {
  .site-mobile-menu {
    display: block;
  }
}

.tarjeta-container {
  width: 75px;
  height: 75px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  border-radius: 40%;
  background: rgba(255, 255, 255, 0.2);
}

.tarjeta-item {
  width: 35px;
}

.tarjeta-container-oferta-educativa {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-content: center;
  border-radius: 40%;
}

.tarjeta-item-oferta-educativa {
  width: 35px;
}

.modal-galeria {
  display: flex;
  justify-content: center;
}

.modal-galeria-imagen {
  display: block;
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}
