@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.navbar-collapse-header-admin {
  background: #343a40 !important;
}

.modal-ancho {
  max-width: 1000px;
}

.figure-img {
  margin-top: 15px;
  height: 250px;
  overflow: hidden;
}

.imagen-usuario {
  height: 250px;
}


.cargando {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.imagen-carga{
  width: 500px;
}

.body-login {
  margin: 0;
  padding: 0;
  background: url(/aula-virtual-insbruk/static/media/login5.af4852b9.jfif) no-repeat 0px 0px;
  min-height: 100vh;
  min-width: 100vw;
  background-size: cover;
  font-family: "Raleway", sans-serif;
}

.contenedor-formulario {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.formulario-login-h2 {
  margin: 0;
  color: var(--rojo-1);
  font-size: 25px;
  font-weight: 400;
  text-align: center;
  letter-spacing: 1px;
  padding-bottom: 30px;
  font-family: "Raleway", sans-serif;
}

.formulario-login {
  background: var(--negro-5);
  width: 500px;
  margin: 0 auto;
  padding: 3em;
  border-radius: 10px;
  box-sizing: border-box;
}

.group {
  display: flex;
  padding: 15px 5px;
  background-color: var(--blanco);
}

.group i {
  color: var(--rojo-1);
  font-size: 20px;
  padding: 0 10px;
}

.formulario-login-group {
  margin: 20px 0;
}

.formulario-login-group .formulario-login-group-label {
  display: block;
  text-transform: uppercase;
  font-size: 13px;
  color: var(--plomo-3);
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-style: italic;
  font-family: "Raleway", sans-serif;
}

.formulario-login-group-input[type="email"],
.formulario-login-group-input[type="password"] {
  width: 100%;
  border: none;
  box-sizing: border-box;
  background: transparent;
  color: var(--negro-1);
  font-family: "Raleway", sans-serif;
}

.formulario-login-group-input[type="email"]:focus,
.formulario-login-group-input[type="password"]:focus {
  outline: none;
  font-family: "Raleway", sans-serif;
}

.formulario-login-group-button {
  background: var(--rojo-1);
  color: var(--blanco);
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  padding: 12px 60px;
  cursor: pointer;
  width: 100%;
  border-radius: 6px;
  font-family: "Raleway", sans-serif;
}

.formulario-login-group-button:hover {
  background-color: var(--naranja-1);
  transition-duration: 5s;
  font-family: "Raleway", sans-serif;
}

.forgot {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.forgot-password {
  color: var(--plomo-3);
  font-size: 14px;
  text-decoration: none;
}

.forgot p {
  color: var(--plomo-3);
  text-align: center;
  margin: 0px;
  font-size: 13px;
}

/*-- /responsive design --*/

@media (max-width: 640px) {
  p.copyright-agileinfo {
    letter-spacing: 1px;
  }
}

@media (max-width: 568px) {
  p.copyright-agileinfo {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .formulario-login {
    margin: 0 3vw;
    max-width: 500px;
  }
  p.copyright-agileinfo {
    line-height: 1.8em;
  }
}

@media (max-width: 320px) {
  .formulario-login {
    margin: 0 3vw;
    max-width: 500px;
  }
  .forgot p {
    text-align: left;
  }
  .forgot {
    display: block;
  }
}

/*-- /responsive design --*/

.contenedor-logo-principal-footer {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.figure-logo-principal-footer {
  flex: 1 1;
  height: 100%;
}

.logo-principal-footer {
  width: 100%;
}

@media (max-width: 992px) {
  .logo-principal-footer {
    width: 80%;
  }
}

@media (max-width: 679px) {
  .logo-principal-footer {
    width: 100%;
  }
}

.menu-lateral {
  text-align: right !important;
  display: block;
}

.site-menu-mobile {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.site-menu-mobile-item {
  color: var(--negro-1);
}

.navbar-light .navbar-toggler {
  color: var(--negro-3);
  background-color: var(--verde-2);
  border-color: var(--verde-1);
  font-size: 1.5rem;
}

.navbar-collapse {
  background: var(--humo-2);
}

.aula-virtual {
  font-size: 9px;
}

.logo-principal {
  height: 42px;
}


@media (min-width: 992px) {
  .site-menu-mobile {
    display: none;
  }

  .barra-menu-contenedor {
    min-height: 50px;
  }

  .barra-menu {
    min-height: 50px;
  }

  .barra-menu-colapso {
    margin-top: 0px;
  }
}

.fondo-principal {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal.e9980280.jpg);
}

.fondo-principal-2 {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal2.5b8db2f1.jfif);
}

.fondo-principal-3 {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal3.fef87690.jfif);
}

.fondo-principal-4 {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal4.7021335b.jfif);
}

.fondo-principal-5 {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal5.03cb113b.jpg);
}

.fondo-principal-6 {
  transition: 2s;
  background-image: url(/aula-virtual-insbruk/static/media/principal6.c6a3a68d.jfif);
}

.fondo-secundario{
    background-image: url(/aula-virtual-insbruk/static/media/maestra.a38f63a7.jpg);
}
.navbar-collapse-header-profesor {
  background: #343a40 !important;
}

.navbar-collapse-header-alumno {
  background: #343a40 !important;
}

.navbar-titulo-header {
  color: rgba(255, 255, 255, 0.5);
}



