body {
  font-family: var(--fuente-mulish), sans-serif;
  line-height: 1.6;
  background-color: var(--blanco);
  overflow-x: hidden;
  font-size: 16px;
  color: var(--negro-2);
}
body:before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  right: 0;
  bottom: 0;
  background: var(--negro-2);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s all ease;
}
body.offcanvas-menu:before {
  opacity: 1;
  visibility: visible;
}

.text-black {
  color: var(--negro-1) !important;
}

.text-secondary {
  color: var(--verde-1) !important;
}

.bg-primary-light {
  background-color: rgba(15, 183, 141, 0.05);
}

.bg-secondary-light {
  background-color: rgba(18, 148, 127, 0.05);
}

a {
  color: var(--rojo-2);
  transition: 0.3s all ease;
}
a:hover {
  color: var(--rojo-2);
  text-decoration: none;
}

::selection {
  background: var(--negro-1);
  color: var(--blanco);
}

.text-primary {
  color: var(--rojo-2) !important;
}

a.text-primary {
  color: var(--rojo-2) !important;
}
a.text-primary:hover {
  color: var(--rojo-2) !important;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
  font-family: var(--fuente-libre), serif;
  color: var(--negro-1);
}

.boton {
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 30px;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.05rem;
}
.boton:active,
.boton:focus {
  outline: none;
  box-shadow: none;
}

.boton.boton-personalizado {
  background: var(--rojo-2);
  border-color: var(--rojo-2);
  color: var(--blanco);
}

.boton.boton-personalizado:hover {
  border-color: var(--rojo-1);
  background: var(--rojo-1);
}

.boton.boton-sm {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
}
.boton.boton-outline-personalizado {
  border: 1px solid var(--rojo-2);
  color: var(--rojo-2);
}
.boton.boton-outline-personalizado:hover {
  background: var(--rojo-2);
  color: var(--blanco);
}

.form-control,
.custom-select {
  border: none;
  font-size: 16px;
  height: 45px;
  background: transparent;
  border: 1px solid var(--negro-3);
  border-radius: 0;
  color: var(--negro-1);
}
.form-control:active,
.form-control:focus,
.custom-select:active,
.custom-select:focus {
  color: var(--negro-1);
  border: 1px solid var(--rojo-2);
  box-shadow: none;
  background: transparent;
}
.form-control::-webkit-input-placeholder,
.custom-select::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: rgba(0, 0, 0, 0.5);
}
.form-control::-moz-placeholder,
.custom-select::-moz-placeholder {
  /* Firefox 19+ */
  color: rgba(0, 0, 0, 0.5);
}
.form-control:-ms-input-placeholder,
.custom-select:-ms-input-placeholder {
  /* IE 10+ */
  color: rgba(0, 0, 0, 0.5);
}
.form-control:-moz-placeholder,
.custom-select:-moz-placeholder {
  /* Firefox 18- */
  color: rgba(0, 0, 0, 0.5);
}

.gutter-v1 {
  margin-right: -20px;
  margin-left: -20px;
}
@media (max-width: 991.98px) {
  .gutter-v1 {
    margin-left: -15px;
    margin-right: -15px;
  }
}
.gutter-v1 > .col,
.gutter-v1 > [class*="col-"] {
  padding-right: 20px;
  padding-left: 20px;
}
@media (max-width: 991.98px) {
  .gutter-v1 > .col,
  .gutter-v1 > [class*="col-"] {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.gutter-v2 {
  margin-right: -5px;
  margin-left: -5px;
}
@media (max-width: 991.98px) {
  .gutter-v2 {
    margin-left: -15px;
    margin-right: -15px;
  }
}
.gutter-v2 > .col,
.gutter-v2 > [class*="col-"] {
  padding-right: 5px;
  padding-left: 5px;
}
@media (max-width: 991.98px) {
  .gutter-v2 > .col,
  .gutter-v2 > [class*="col-"] {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.gutter-v3 {
  margin-right: -40px;
  margin-left: -40px;
}
@media (max-width: 991.98px) {
  .gutter-v3 {
    margin-left: -15px;
    margin-right: -15px;
  }
}
.gutter-v3 > .col,
.gutter-v3 > [class*="col-"] {
  padding-right: 40px;
  padding-left: 40px;
}
@media (max-width: 991.98px) {
  .gutter-v3 > .col,
  .gutter-v3 > [class*="col-"] {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.spinner-border {
  color: var(--rojo-2);
}

/*PRELOADING------------ */
#overlayer {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 7100;
  background: var(--blanco);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.loader {
  z-index: 7700;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.top-bar a {
  color: var(--blanco);
}

.logo {
  font-size: 24px;
  color: var(--blanco);
  font-weight: 700;
  left: 15px;
}
@media (max-width: 991.98px) {
  .logo {
    left: 20px;
  }
}
.logo:hover {
  color: var(--blanco);
}

.menu-absolute {
  position: absolute !important;
}

.boton-book {
  position: absolute;
  right: 15px;
  margin-top: -2px;
}
@media (max-width: 991.98px) {
  .boton-book {
    right: 60px;
  }
}

.site-nav {
  position: absolute;
  width: 100%;
  z-index: 9;
  top: 0;
}
.site-nav a {
  position: relative;
  z-index: 9;
  cursor: pointer;
}

.site-nav {
  padding-top: 20px;
  padding-bottom: 20px;
}
.site-nav h1 {
  margin: 0;
  padding: 0;
  font-size: 24px;
}
.site-nav .site-navigation .site-menu {
  margin-bottom: 0;
}
.site-nav .site-navigation .site-menu > li {
  display: inline-block;
}
.site-nav .site-navigation .site-menu > li > a {
  font-size: 14px;
  padding: 10px 15px;
  display: inline-block;
  text-decoration: none !important;
  color: rgba(255, 255, 255, 0.5);
  position: relative;
}
.site-nav .site-navigation .site-menu > li > a:hover {
  color: var(--blanco);
}
.site-nav .site-navigation .site-menu > li.cta-button a {
  padding: 5px 22px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  color: var(--negro-1);
  border-radius: 30px;
}
.site-nav .site-navigation .site-menu > li.cta-button a:hover {
  color: var(--blanco);
  background: var(--blanco);
  border-color: var(--blanco);
}
.site-nav .site-navigation .site-menu > li.cta-button.active a {
  color: var(--negro-1);
  background: var(--blanco);
  border-color: var(--blanco);
}
.site-nav .site-navigation .site-menu > li.active > a {
  color: var(--blanco);
}
.site-nav .site-navigation .site-menu .has-children {
  position: relative;
}
.site-nav .site-navigation .site-menu .has-children > a {
  position: relative;
  padding-right: 20px;
}
.site-nav .site-navigation .site-menu .has-children > a:before {
  position: absolute;
  content: "\e313";
  font-size: 14px;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-family: "icomoon";
}
.site-nav .site-navigation .site-menu .has-children .dropdown {
  visibility: hidden;
  opacity: 0;
  top: 100%;
  z-index: 999;
  position: absolute;
  text-align: left;
  box-shadow: 0 2px 10px -2px rgba(0, 0, 0, 0.1);
  padding: 20px 0;
  margin-top: 20px;
  margin-left: 0px;
  background: var(--blanco);
  transition: 0.2s 0s;
}
.site-nav .site-navigation .site-menu .has-children .dropdown.arrow-top {
  position: absolute;
}
.site-nav .site-navigation .site-menu .has-children .dropdown.arrow-top:before {
  display: none;
  bottom: 100%;
  left: 20%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.site-nav .site-navigation .site-menu .has-children .dropdown.arrow-top:before {
  display: none;
  border-color: rgba(136, 183, 213, 0);
  border-bottom-color: var(--blanco);
  border-width: 10px;
  margin-left: -10px;
}
.site-nav .site-navigation .site-menu .has-children .dropdown a {
  font-size: 14px;
  text-transform: none;
  letter-spacing: normal;
  transition: 0s all;
  color: var(--negro-1);
}
.site-nav .site-navigation .site-menu .has-children .dropdown .active {
  color: var(--rojo-2) !important;
}
.site-nav .site-navigation .site-menu .has-children .dropdown > li {
  list-style: none;
  padding: 0;
  margin: 0;
  min-width: 180px;
}
.site-nav .site-navigation .site-menu .has-children .dropdown > li > a {
  padding: 5px 20px;
  display: block;
}
.site-nav .site-navigation .site-menu .has-children .dropdown > li > a:hover {
  color: var(--rojo-2);
}
.site-nav .site-navigation .site-menu .has-children .dropdown > li.active > a {
  color: var(--rojo-2);
}
.site-nav
  .site-navigation
  .site-menu
  .has-children
  .dropdown
  > li.has-children
  > a:before {
  content: "\e315";
  right: 20px;
}
.site-nav
  .site-navigation
  .site-menu
  .has-children
  .dropdown
  > li.has-children
  > .dropdown,
.site-nav
  .site-navigation
  .site-menu
  .has-children
  .dropdown
  > li.has-children
  > ul {
  left: 100%;
  top: 0;
